// sjcl
const sjcl = require('./sjcl');

// 
window.onload = function () {
  // 
  const input = document.getElementById('file');
  const input2 = document.getElementById('file2');
  input.addEventListener('change', importImage);
  input2.addEventListener('change', importImage2);

  // 
  const encodeButton = document.getElementById('encode');
  encodeButton.addEventListener('click', encode);

  // 
  const decodeButton = document.getElementById('decode');
  decodeButton.addEventListener('click', decode);
};

// 
const maxMessageSize = 1011; // hash,0.986kb

// canvas
const importImage = function (e) {

  if (!e.target.files.length) {
    return
  }

  const reader = new FileReader();

  reader.onload = function ({ target }) {

    // 
    document.getElementById('preview').style.display = 'block';
    document.getElementById('preview').src = target.result;

    // 
    document.getElementById('message').value = '';
    document.getElementById('password').value = '';
    document.getElementById('password2').value = '';
    document.getElementById('messageDecoded').innerHTML = '';

    // canvas
    const img = new Image();
    img.onload = function () {
      let ctx = document.getElementById('canvas').getContext('2d');
      ctx.canvas.width = img.width;
      ctx.canvas.height = img.height;
      ctx.drawImage(img, 0, 0);
      console.time('decode');
      decode();
      console.timeEnd('decode');
    };
    img.src = target.result;
  };

  reader.readAsDataURL(e.target.files[0]);
};

// 
const importImage2 = function (e) {

  if (!e.target.files.length) {
    return
  }

  const reader = new FileReader();

  reader.onload = function ({ target }) {
    const { result } = target;
    // 
    document.getElementById('preview2').style.display = 'block';
    document.getElementById('preview2').src = result;
    if (result.length > maxMessageSize) {
      alert('0.9Kb,')
    } else {
      document.getElementById('message').value = result;
    }
  };

  reader.readAsDataURL(e.target.files[0]);
};

// 
const encode = function () {
  let message = document.getElementById('message').value;
  let password = document.getElementById('password').value;
  let output = document.getElementById('output');
  let canvas = document.getElementById('canvas');
  let ctx = canvas.getContext('2d');

  // 
  if (password.length > 0) {
    message = sjcl.encrypt(password, message);
  } else {
    message = JSON.stringify({ 'text': message });
  }

  // 
  let pixelCount = ctx.canvas.width * ctx.canvas.height;
  if ((message.length + 1) * 16 > pixelCount * 4 * 0.75) {
    alert('');
    return;
  }

  // 
  if (message.length > maxMessageSize) {
    alert('');
    return;
  }

  // 
  const imgData = ctx.getImageData(0, 0, ctx.canvas.width, ctx.canvas.height);
  encodeMessage(imgData.data, sjcl.hash.sha256.hash(password), message);
  ctx.putImageData(imgData, 0, 0);

  output.src = canvas.toDataURL();

  // 
  alert('');

};

// 
const decode = function () {
  const password = document.getElementById('password2').value;
  const passwordFail = '';

  // 
  const ctx = document.getElementById('canvas').getContext('2d');
  const imgData = ctx.getImageData(0, 0, ctx.canvas.width, ctx.canvas.height);
  const message = decodeMessage(imgData.data, sjcl.hash.sha256.hash(password));

  // try to parse the JSON
  let obj = null;
  try {
    obj = JSON.parse(message);
  } catch (e) {

    //   / 
    document.getElementById('choose').style.display = 'block';
    document.getElementById('chooseImage').style.display = 'block';
    document.getElementById('reveal').style.display = 'none';

    if (password.length > 0) {
      alert(passwordFail);
    }
  }

  // 
  if (obj) {
    document.getElementById('choose').style.display = 'none';
    document.getElementById('chooseImage').style.display = 'none';
    document.getElementById('reveal').style.display = 'block';

    // 
    if (obj.ct) {
      try {
        obj.text = sjcl.decrypt(password, message);
      } catch (e) {
        alert(passwordFail);
      }
    }

    // 
    const escChars = {
      '&': '&amp;',
      '<': '&lt;',
      '>': '&gt;',
      '"': '&quot;',
      '\'': '&#39;',
      '/': '&#x2F;',
      '\n': '<br/>'
    };
    const escHtml = function (string) {
      return String(string).replace(/[&<>"'\/\n]/g, function (c) {
        return escChars[c];
      });
    };
    const { text } = obj;
    const content = document.getElementById('messageDecoded');
    // 
    if (text.startsWith('data:image')) {
      const img = new Image();
      img.src = text;
      content.innerHTML = '';
      content.appendChild(img)
    } else {
      content.innerHTML = escHtml(text);
    }
  }
};

//  01
const getBit = function (number, location) {
  return ((number >> location) & 1);
};

//  01
const setBit = function (number, location, bit) {
  return (number & ~(1 << location)) | (bit << location);
};

// 210
const getBitsFromNumber = function (number) {
  const bits = [];
  for (let i = 0; i < 16; i++) {
    bits.push(getBit(number, i));
  }
  return bits;
};

// 2
const getNumberFromBits = function (bytes, history, hash) {
  let number = 0, pos = 0;
  while (pos < 16) {
    let loc = getNextLocation(history, hash, bytes.length);
    let bit = getBit(bytes[loc], 0);
    number = setBit(number, pos, bit);
    pos++;
  }
  return number;
};

// 10
const getMessageBits = function (message) {
  let messageBits = [];
  for (let i = 0; i < message.length; i++) {
    let code = message.charCodeAt(i);
    messageBits = messageBits.concat(getBitsFromNumber(code));
  }
  return messageBits;
};

// bit
const getNextLocation = function (history, hash, total) {
  let pos = history.length;
  let loc = Math.abs(hash[pos % hash.length] * (pos + 1)) % total;
  while (true) {
    if (loc >= total) {
      loc = 0;
    } else if (history.indexOf(loc) >= 0) {
      loc++;
    } else if ((loc + 1) % 4 === 0) {
      loc++;
    } else {
      history.push(loc);
      return loc;
    }
  }
};

/**
 * 10
 * RGB
 */
const encodeMessage = function (colors, hash, message) {
  // 
  let messageBits = getBitsFromNumber(message.length);
  messageBits = messageBits.concat(getMessageBits(message));

  // 
  const history = [];

  // bit
  let pos = 0;
  while (pos < messageBits.length) {
    // set the next color value to the next bit
    let loc = getNextLocation(history, hash, colors.length);
    colors[loc] = setBit(colors[loc], 0, messageBits[pos]);

    // Alpha255, alpha
    // example: http://stackoverflow.com/q/4309364
    while ((loc + 1) % 4 !== 0) {
      loc++;
    }
    colors[loc] = 255;

    pos++;
  }
};

//  CanvasPixel Array 'colors' 
const decodeMessage = function (colors, hash) {
  // 
  const history = [];

  // 
  const messageSize = getNumberFromBits(colors, history, hash);

  if ((messageSize + 1) * 16 > colors.length * 0.75) {
    return '';
  }

  if (messageSize === 0 || messageSize > maxMessageSize) {
    return '';
  }

  // 
  const message = [];
  for (let i = 0; i < messageSize; i++) {
    const code = getNumberFromBits(colors, history, hash);
    message.push(String.fromCharCode(code));
  }

  return message.join('');
};